<template lang="pug">
.listItem(v-if="connect" :class="{'unRead': !item.isRead}" @click="connect")
  .avatar(:style="getAvatar")
    OnlineStatus(:status="item.isOnline")
  .item
    .itemUserName {{ item.userName }}
    .itemUserMsg {{ item.lastMessage }}
  .itemInfo
    .itemTimestamp {{ $formatTime(item.senderTime, 'MM/DD') }}
</template>

<script>

export default {
  name: 'ListItem',
  props: {
    userID: {
      type: String,
      default: () => null
    },
    item: {
      type: Object,
      default: () => null,
    }
  },
  components: {
    OnlineStatus: () => import('./OnlineStatus.vue'),
  },
  computed: {
    getAvatar() { // API取列表會拿到，開啟對話後由此參數傳入聊天室，新增聯絡人則從外部處理
      const { stickerUrl } = this.item;
      return stickerUrl
        ? `background-image: url(${stickerUrl});`
        : 'background-color: #212529';
    },
  },
  methods: {
    connect() {
      const { conversationID, userID, userName, isOnline } = this.item;
      this.$emit('connect', { conversationID, userID, userName, isOnline, stickerUrl: this.getAvatar });
    },
  }
}
</script>

<style lang="scss">
.listItem {
  padding: 8px 12px;
  display: flex;
  align-items: start;
  border-radius: 4px;
  cursor: pointer;
  &.unRead {
    background-color: #fbfbfb;
    font-weight: 600;
  }
  &:hover, &.unRead:hover {
    background-color: #f7f7f7;
  }
  .avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    margin-right: 8px;
    margin-top: 6px;
    position: relative;
    background-size: cover;
    background-position: center;
  }
  .item {
    font-size: 14px;
    width: calc(100% - 86px);
    .itemUserName {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      padding-right: 4px;
      margin-bottom: 6px;
    }
    .itemUserMsg {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      max-width: 140px;
    }
  }
  .itemInfo {
    width: 30px;
    padding-right: 6px;
    .itemTimestamp {
      font-size: 12px;
      margin-bottom: 16px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 50px;
    }
  }
}
</style>